.services__list-item {
  max-width: 280px;
  padding: 40px 20px;
  box-sizing: border-box;

  border: 1px solid #f0ece8;
  border-radius: 12px;
  background-color: #fff;
  box-shadow: 0 2px 12px 0 rgb(43 43 43 / 6%);

  display: flex;
  flex-direction: column;
  align-items: center;

  transition: 300ms ease;
}

@media screen and (max-width: 1280px) {
  .services__list-item {
    max-width: 320px;
  }
}
@media screen and (max-width: 768px) {
  .services__list-item {
    max-width: 100%;
  }
}

.services__list-item_active {
  transform: translate3d(0px, -3px, 0.01px);
}

.services__item-title {
  margin: 0;
  font-weight: 400;
  font-size: 20px;
  line-height: 30px;
  color: #000;
  text-align: center;

  transition: 300ms ease;
}

.services__item-title_active {
  color: #ff9c08;
}

.services__item-text {
  margin: 8px 0 0;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  color: #6e6e6e;
}

.services__item-button {
  display: block;
  width: 100%;
  margin: 16px 0 0;
  padding: 18px 24px;
  box-sizing: border-box;
  border-radius: 10px;
  border: 1px solid #f0ece8;
  background-color: transparent;

  text-decoration: none;
  font-weight: 400;
  font-size: 18px;
  line-height: 20px;
  text-align: center;
  color: #000;

  transition: 300ms ease;
}

.services__item-button:hover {
  cursor: pointer;
  background-color: #f08200;
  color: #fff;
}
