.work {
  width: 100%;
  max-width: 1440px;
  padding: 100px 0;
  box-sizing: border-box;
  background-color: #181818;
  color: #fff;

  display: grid;
  justify-content: center;
  gap: 40px;
}
@media screen and (max-width: 1280px) {
  .work {
    padding: 50px 0;
  }
}
@media screen and (max-width: 480px) {
  .work {
    padding: 50px 0 0;
  }
}

.work__title {
  margin: 0;
  text-transform: uppercase;
  justify-self: center;
  font-weight: 400;
  font-size: 25px;
  line-height: 30px;
  color: #fff;
}

.work__article {
  display: flex;
  justify-content: center;
  transition: all 300ms ease;
}

.work__list {
  justify-self: center;
  width: 100%;
  max-width: 1260px;
  list-style-type: none;
  margin: 0;
  padding: 0;
  box-sizing: border-box;

  display: flex;
  gap: 20px;

  overflow-x: hidden;
  scroll-behavior: smooth;
  transition: all 300ms ease;
}
@media screen and (max-width: 1440px) {
  .work__list {
    max-width: 940px;
  }
}
@media screen and (max-width: 1280px) {
  .work__list {
    max-width: 620px;
  }
}
@media screen and (max-width: 960px) {
  .work__list {
    width: 100%;
    max-width: 300px;
  }
}
@media screen and (max-width: 480px) {
  .work__list {
    overflow-x: auto;
    max-width: 100%;
    gap: 0;
  }
}

.work__list-item {
  min-width: 300px;
  box-sizing: border-box;
  border: 2px solid #444;
  border-radius: 15px;
  display: flex;
}
@media screen and (max-width: 1024px) {
  .work__list-item {
    cursor: grabbing;
  }
}
@media screen and (max-width: 480px) {
  .work__list-item {
    min-width: 100%;
    border: none;
  }
}

.work__image {
  align-self: center;
  width: 100%;
  aspect-ratio: 2 / 3;
  border-radius: 12px;
  object-fit: cover;
  object-position: center;
}
@media screen and (max-width: 480px) {
  .work__image {
    border: none;
    border-radius: 0;
  }
}

.work__button {
  margin: 20px;
  padding: 0;
  align-self: center;
  background-color: transparent;
  border: none;
  font-size: 40px;
  color: #444;

  transition: all 300ms ease;
}
@media screen and (max-width: 480px) {
  .work__button {
    display: none;
  }
}

.work__button:hover {
  color: #fff;
  cursor: pointer;
}

