.article {
  width: 100%;
  max-width: 768px;
  border-style: solid;
  border-width: 1px;
  border-color: #f0ece8;
  border-radius: 12px;
  background-color: #fff;
  box-shadow: 0 2px 12px 0 rgb(43 43 43 / 6%);
  margin: 50px 0;
  padding: 100px 70px;
  box-sizing: border-box;

  display: flex;
  flex-direction: column;
  align-items: center;
}
@media screen and (max-width: 1024px) {
  .article {
    margin: 0;
    padding: 50px 30px;
    border: none;
    box-shadow: none;
  }
}
@media screen and (max-width: 768px) {
  .article {
    padding: 50px 20px;
  }
}

.article__image {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin: 0 0 24px;
  object-fit: cover;
  object-position: center;
}

.article__title {
  margin: 0 0 16px;
  font-weight: 400;
  font-size: 25px;
  line-height: 30px;
  color: #000;
  text-align: center;
}

.article__links {
  list-style-type: none;
  margin: 0 0 40px;
  padding: 0;

  display: flex;
  gap: 16px;
}

.article__link {
  display: block;
  width: 36px;
  height: 36px;
  background-color: #090909;
  border-radius: 8px;
  transition: 300ms ease;
}

.article__link:hover {
  background-color: #ff9c08;
  transform: translate3d(0px, -3px, 0.01px);
}

.article__link-image {
  width: 100%;
  background-color: transparent;
  border-radius: 8px;
  object-fit: contain;
  object-position: center;
}

.article__article {
  border-top: 1px solid #dad1ca;
  padding-top: 40px;
}

.article__subtitle {
  margin: 0 0 24px;
  font-family: var(--h2-font-family);
  font-weight: var(--h2-font-weight);
  font-size: var(--h2-font-size-768px);
  line-height: var(--h2-line-height-768px);
  color: var(--h2-color);
}

.article__text {
  margin: 0 0 24px;
  font-family: var(--text-font-family);
  font-weight: var(--text-font-weight);
  font-size: var(--text-font-size-1024px);
  line-height: var(--text-line-height-1024px);
  color: var(--text-color-gray);
}

@media screen and (max-width: 768px) {
  .article__text {
    font-size: var(--text-font-size-768px);
    line-height: var(--text-line-height-768px);
  }
}

.article__text:last-of-type {
  margin: 0;
  font-family: var(--text-font-family);
  font-weight: var(--text-font-weight);
  font-size: var(--text-font-size-1024px);
  line-height: var(--text-line-height-1024px);
  color: var(--text-color-gray);
}

.article__list {
  list-style-type: disc;
  margin: 0 0 24px;

  display: flex;
  flex-direction: column;
  gap: 8px;

  font-family: var(--text-font-family);
  font-weight: var(--text-font-weight);
  font-size: var(--text-font-size-1024px);
  line-height: var(--text-line-height-1024px);
  color: var(--text-color-gray);
}

@media screen and (max-width: 768px) {
  .article__list {
    padding-left: 20px;
    font-size: var(--text-font-size-768px);
    line-height: var(--text-line-height-768px);
  }
}

.article__item {
  margin: 0;
  padding-left: 18px;
}

@media screen and (max-width: 768px) {
  .article__item {
    padding-left: 0;
  }
}
